// @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";

#custom_loader {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: url("/images/loader.gif") center no-repeat #fff;
  display: none;
}

.jumbotron {
  background: url("/images/croppedcat.jpg") center no-repeat;
  text-shadow: 0 0 1em black;
  color: white;
  margin-bottom: 0px;
}

html {
  position: relative;
  min-height: 100%;
}

body {
  margin-bottom: 4.5em;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 4.5em;
  background-color: #f5f5f5;
}

.footer p {
  margin: 1em auto 0 auto;
}

.content {
  margin-top: 2em;
}

.clickable {
  cursor: pointer;
}

.large-gray {
  color: #999;
  font-size: 1.5em;
}

.hide_class {
  display: none;
}

.list-group.location {
  padding: 0px 7px;
  margin-bottom: 1em;

  @media (min-width: 750px) {
    &:nth-of-type(2n+1) {
      clear: both;
    }
  }

  @media (min-width: 970px) {
    &:nth-of-type(2n+1) {
      clear: none;
    }

    &:nth-of-type(3n+1) {
      clear: both;
    }
  }
}

.cheese {
  margin-bottom: 0;
}

.list-group.stage {
  margin-bottom: 0;
  border-left: 1px solid lightgray;
  border-right: 1px solid lightgray;
  border-bottom: 1px solid lightgray;

  .stage-name {
    background-color: lightgray;
  }
}

.gray {
  color: gray;
}

#invalid_mice_title {
  background-color: #f2dede;
  color: #a94442;
}

.collapse-toggle:after {
  font-family: 'Glyphicons Halflings';
  content: "\e113";
  float: right;
}

.collapse-toggle.collapsed:after {
  content: "\e114";
}

.collapse-toggle-two:after {
  font-family: 'Glyphicons Halflings';
  content: "\2212";
  float: right;
}

.collapse-toggle-two.collapsed:after {
  content: "\2b";
}

.no-left-padding {
  padding-left: 0px;
}
